* {
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box
}

article,
aside,
audio,
body,
dd,
div,
dl,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
iframe,
input,
label,
menu,
nav,
ol,
p,
section,
select,
td,
textarea,
th,
time,
ul,
video {
	margin: 0;
	padding: 0
}

article,
aside,
audio,
canvas,
footer,
header,
hgroup,
nav,
section,
video {
	display: block
}

body {
	font-family: Microsoft YaHei, Helvetica, STHeiti, Droid Sans Fallback;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0)
}

textarea {
	resize: none
}

iframe,
img {
	border: 0
}

img {
	width: 100%;
	height: auto;
	vertical-align: middle
}

ol,
ul {
	list-style: none
}

cite {
	font-style: normal
}

h1,
h2,
h3,
h4 {
	font-weight: 400
}

input,
select,
textarea {
	outline: 0;
	font-family: Microsoft YaHei;
	-webkit-user-select: text;
	-moz-user-select: text;
	-ms-user-select: text;
	user-select: text
}

input {
	-webkit-appearance: none;
	border-radius: 0
}

a {
	text-decoration: none
}

.flex {
	display: -ms-flexbox;
	display: flex
}

.flex-item {
	-ms-box-flex: 1.0;
	flex: 1
}

.flex-none {
	-webkit-box: none;
	flex: none
}

.flex-row {
	flex-direction: row
}

.flex-column {
	flex-direction: column
}

.flex-row-reverse {
	flex-direction: row-reverse
}

.flex-column-reverse {
	flex-direction: column-reverse
}

.flex-wrap {
	flex-wrap: wrap
}

.flex-nowrap {
	flex-wrap: nowrap
}

.justify-content-start {
	box-pack: start;
	justify-content: flex-start
}

.justify-content-end {
	box-pack: end;
	justify-content: flex-end
}

.justify-content-center {
	box-pack: center;
	justify-content: center
}

.justify-content-between {
	box-pack: justify;
	justify-content: space-between
}

.justify-content-around {
	justify-content: space-around
}

.align-items-start {
	box-align: start;
	align-items: flex-start
}

.align-items-end {
	box-align: end;
	align-items: flex-end
}

.align-items-center {
	box-align: center;
	align-items: center
}

.align-self-start {
	align-self: flex-start
}

.align-self-end {
	-ms-flex-item-align: end;
	align-self: flex-end
}

.align-self-center {
	align-self: center
}

.fs-12 {
	font-size: 12px
}

.fs-14 {
	font-size: 14px
}

.fs-16 {
	font-size: 16px
}

.fs-18 {
	font-size: 18px
}

.fs-20 {
	font-size: 2px
}

.alert-wrap {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	pointer-events: auto
}

.alert-wrap p {
	min-width: .6rem;
	max-width: 3rem;
	display: inline-block;
	border-radius: 1px;
	color: #fff;
	background: rgba(58, 58, 58, .9);
	border-radius: .03rem;
	line-height: 1.18;
	padding: 9px 15px;
	text-align: center
}

.loading {
	position: absolute;
	top: 0;
	left: 0;
	width: 8px;
	height:35px;
	z-index: 1
}

.loading .loader {
	position: relative;
	width: 3px;
	height: 3px;
	border: .02rem solid hsla(0, 0%, 100%, .4);
	border-radius: 50%
}

.loading .loader:after {
	content: "";
	position: absolute;
	top: -.02rem;
	right: -.02rem;
	bottom: -.02rem;
	left: -.02rem;
	border-radius: .5rem;
	border: .02rem solid transparent;
	border-top-color: #fff;
	animation: a 1s linear infinite;
	-webkit-animation: a 1s linear infinite
}

@keyframes a {
	0% {
		transform: rotate(0deg)
	}
	to {
		transform: rotate(1turn)
	}
}

.btn,
.text-center {
	text-align: center
}

.btn {
	display: block;
	outline: none;
	border: 0;
	padding: 0;
	background: transparent;
	font-family: inherit
}

.btn.btn-blue {
	background-color: #27a1e5
}

.btn.disabled,
.btn:disabled {
	cursor: not-allowed
}

.btn-block {
	display: block;
	width: 100%;
	height: 42px;
	line-height: 42px;
	font-size: 15px;
	color: #fff
}

.btn-block.disabled,
.btn-block:disabled {
	background-color: #bce1f5
}

.btn-block.btn-radius {
	border-radius: .21rem
}

.full-bg {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background: url(data:image/png;base64,) 0 0 no-repeat;
	background-size: 100%
}

.logo-wrap {
	width: 100%;
	padding: 35px 0 15px;
	text-align: center
}

.logo-wrap img {
	display: block;
	width: 77px;
	height:77px;
	margin: 0 auto;
	
}

.trc-key {
	font-size: 16px;
	text-align: center;
	color: #666
}

.login-wrap {
	padding: 30px 40px;
}

.login-tab .tab {
	display: inline-block;
	width: 50%;
	border-bottom: 1px solid #ccc;
	color: #1c1c20;
	font-size: 15px;
	line-height: 35px;
	text-align: center
}

.login-tab .tab.act {
	border-bottom: 1px solid #27a1e5
}

.input-container {
	margin-top: 24px
}

.input-item {
	position: relative;
	width: 100%;
	height:48px;
	border: 1px solid #e5e5e5;
	margin-bottom: 20px;
	padding-left: 20px
}

.input-item input {
	display: block;
	border: 0;
	outline: none;
	width: 100%;
	font-size: 16px;
	line-height: 48px;
	background-color: transparent
}

.input-item input::-webkit-input-placeholder {
	font-size: 16px;
	color: #ccc
}

.input-item.focus-border input {
	height: 48px;
	padding: 12px 0
}

.input-item .sms-code {
	position: absolute;
	top: .1rem;
	right: .1rem;
	width: .8rem;
	height: .28rem;
	line-height: .26rem;
	border: 1px solid #ccc;
	border-radius: .14rem;
	color: #666;
	font-size: .12rem;
	text-align: center;
	cursor: pointer
}

.input-item .sms-code.act {
	color: #27a1e5;
	border: 1px solid #27a1e5;
	margin-right: 10px
}

.input-item .captcha {
	position: absolute;
	top: 6.5px;
	right: 0;
	width: 8px;
	height: 3.5px;
	font-size: .12rem;
	text-align: center;
	cursor: pointer;
	background-color: silver
}

.input-item .captcha img,
.input-item button {
	width: 100%;
	height: 100%;
	display: block
}

.input-item button {
	font-size: .16rem;
	color: #ccc;
	position: relative;
	right: 20px;
	text-align: left;
	padding-left: 20px;
	border: 0;
	background: border-box
}

.input-item button:focus {
	outline: none
}

.login-btn {
	margin-top: 3px
}

.help-block {
	color: #999;
	margin-top: .08rem
}

.help-block a,
.new-user {
	color: #27a1e5
}

.new-user {
	position: absolute;
	width: 100%;
	left: 0;
	bottom: 3px;
	text-align: center
}

.input-wrap .icon {
	position: absolute;
	right: .08rem
}

.input-wrap .icon.icon-eye-close {
	width: 19.5px;
	height: 11px;
	top: 18.5px;
	background: url(data:image/png;base64,) 50% no-repeat;
	background-size: 100%
}

.input-wrap .icon.icon-eye-open {
	width: 185px;
	height: 125px;
	top: .1775rem;
	background: url(data:image/png;base64,) 50% no-repeat;
	background-size: 100%
}

.forget-psd a {
	display: block;
	font-size: 14px;
	color: #ccc;
	text-align: right;
	margin-top: 12px
}

.forget-psd.blue a {
	color: #27a1e5;
	margin-top: -20px
}

.reset-container {
	position: relative;
	background-color: #fff;
	padding: 15px;
}
.label-input {
	border-bottom: 1px solid #e5e5e5;
	width: 100%;
	height: 48px;
}

.label-input label {
	display: block;
	font-size: 16px;
	color: #999;
	width: 9px
}

.label-input input {
	display: block;
	font-size: 16px;
	border: 0;
	outline: none;
	border-radius: 0
}

.forget-psd a span {
	position: absolute;
	width: 100%;
	left: 0;
	bottom: .05rem;
	text-align: center;
	color: #27a1e5
}